<template>
  <v-data-table :headers="headers" :items="portfolios" class="elevation-1">
    <template v-slot:top>
      <v-toolbar flat>
        <v-toolbar-title>Portfolio</v-toolbar-title>
        <v-divider class="mx-4" inset vertical></v-divider>
        <v-spacer></v-spacer>
        <v-dialog v-model="dialog" max-width="500px">
          <v-card>
            <v-card-title>
              <span class="text-h5">Add to my portfolio</span>
            </v-card-title>

            <v-card-text>
              <v-container>
                <v-row>
                  <v-col cols="12" sm="6" md="4">
                    <v-text-field
                      disabled
                      :value="editedItem.id"
                      label="Portfolio ID"
                    ></v-text-field>
                    <v-text-field
                      v-model="editedItem.name"
                      label="Portfolio name"
                    ></v-text-field>
                  </v-col>
                </v-row>
              </v-container>
            </v-card-text>

            <v-card-actions>
              <v-spacer></v-spacer>
              <v-btn color="blue darken-1" text @click="close"> Cancel </v-btn>
              <v-btn color="blue darken-1" text @click="save"> Save </v-btn>
            </v-card-actions>
          </v-card>
        </v-dialog>
      </v-toolbar>
    </template>
    <template v-slot:item.actions="{ item }">
      <v-btn @click="editItem(item)" text>Add Portfolio</v-btn>
    </template>
  </v-data-table>
</template>

<script>
import axios from "../../api/index";
export default {
  data: () => ({
    dialog: false,
    headers: [
      { text: "ID", value: "id" },
      {
        text: "Portfolio",
        align: "start",
        sortable: false,
        value: "name",
      },
      { text: "Actions", value: "actions", sortable: false },
    ],
    portfolios: [],
    editedIndex: -1,
    editedItem: {
      name: "",
    },
    defaultItem: {
      name: "",
    },
  }),

  watch: {
    dialog(val) {
      val || this.close();
    },
  },

  created() {
    axios
      .get("/api/v1/portfolio")
      .then((res) => {
        console.log(res);
        this.portfolios = res.data.product;
      })
      .catch((error) => {
        console.log(error);
      });
  },

  methods: {
    editItem(item) {
      this.editedItem = Object.assign({}, item);
      this.dialog = true;
    },

    close() {
      this.dialog = false;
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem);
        this.editedIndex = -1;
      });
    },

    save() {
      axios
        .post("/api/v1/uportfolio", {
          portfolio_id: this.editedItem.id,
          name: this.editedItem.name
        })
        .then((user) => {
          this.$router.push({ path: "/" });
          console.log(user);
        })
        .catch((error) => {
          console.log(error);
        });
      this.close();
    },
  },
};
</script>